// mapbox.scss

.map {
    margin-top: 4px; //to see underling of note metadata
    height: 200px;
}

.marker {
    background-image: url(data:image/png;base64,);
    background-size: contain;
    width: 20px;
    height: 20px;
}

.map-menu {
    position: absolute;
    top: 0;
    left: 0;
    background: white;
    padding: 0.4rem;
}

.map-menu label {
    margin-left: 3px;
    margin-right: 3px;
}
